div, p, span, img, a, ul, h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
  font-size: 100%;
}

#simulation-container {
  position: fixed;
  top: 0;
  z-index: -1;
}

#banner-container {
  position: absolute;
  bottom: 1vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#banner {
  text-align: center;
  font-size: 3em;
}
.arrow-down {
  margin-top: 20px;
  position: relative;
  width: 50px;
  height: 50px;
}
.arrowL {
  position: absolute;
  left: 0;
  top: 10px;
  content: "";
  background-color: black;
  width: 74%;
  height: 5px;
  transform-origin: left;
  transform: rotate(45deg);
  border-radius: 50px;
  transition: background-color 0.2s ease-in-out;
}
.arrowR {
  position: absolute;
  right: 0;
  top: 10px;
  content: "";
  background-color: black;
  width: 74%;
  height: 5px;
  transform-origin: right;
  transform: rotate(-45deg);
  border-radius: 50px;
  transition: background-color 0.2s ease-in-out;
}
.arrowR.hover, .arrowL.hover {
  background-color: lightgray;
}

#about-me {
  background-color: rgb(7, 182, 7);
  color: white;
  font-size: 5vw;
  text-align: left;
  display: block;
  max-width: 100%;
  padding: 10px 10px;
  margin-top: 100vh;
}
#strengths-header {
  text-align: center;
  font-size: 3em;
  margin: auto;
  width: 250px;
  position: relative;
}
#strengths-header::before {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  background-color: black;
  width: 100%;
  height: 5px;
}
#strengths {
  width: 100%;
  text-align: center;
}
.strength {
  width: 30vw;
  max-width: 200px;
  min-width: 50px;
  display: inline-block;
  margin: 20px;
  font-size: 1.5em;
}
.strength img {
  width: 100%;
  height: auto;
}

#works {
  margin: 0px 10px 0px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.work {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 650px;
}
.work h2 {
  font-size: 2em;
}
.work a {
  margin-top: 10px;
  text-decoration: none;
  color: black;
  font-size: 1.5em;
}
.work img {
  max-width: 100%;
  margin: 10px 0px 10px 0px;
}